@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
    url("../fonts/oswaldregular.woff2") format("woff2"),
    url("../fonts/oswaldregular.woff") format("woff");
}

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src:
    url("../fonts/oswaldmedium.woff2") format("woff2"),
    url("../fonts/oswaldmedium.woff") format("woff");
}

:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}

:root {
  --main-text-color: #444444;
  --accent-text-color: #000000;
  --additional-text-color: #222222;
  --action-color: #ffffff;

  --main-bg-color: #68b738;
  --accent-bg-color: #f2f2f2;
}

.page {
  font-family: var(--main-font);
}

.page__body {
  margin: 0;
  color: var(--main-text-color);
}

.page__centerer {
  margin: 0 20px;

  &--form_padding {
    @media (--middle-vp) {
      padding: 0 40px;
    }
  }

  &--nav-menu_position {
    display: flex;
    padding-top: 42px;
  }

  &--footer_position {
    display: flex;
    flex-direction: column;
  }

  @media (--middle-vp) {
    margin: 0 30px;
  }

  @media (--wide-vp) {
    margin: 0 auto;
    width: 1220px;
  }
}

@custom-media --small-vp (width <= 767.9px);

@custom-media --not-small-vp (width >= 768px);

@custom-media --middle-vp (768px <= width < 1279.9px);

@custom-media --wide-vp (width >= 1280px);

img {
  /*width: 100%;*/
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  font-family: var(--accent-font);
  font-weight: 400;
  color: var(--action-color);
  background-color: var(--main-bg-color);
  width: fit-content;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  border: none;

  &--show-all_color {
    color: var(--main-text-color);
    background-color: var(--accent-bg-color);
  }
}

.field {
  border: none;
}

.field__contacts-legend {
  padding: 0;
  margin-bottom: 35px;
}

.field__extra-legend {
  padding: 0;
  margin-bottom: 34px;
}

.legend {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: var(--inner-title-size);
  line-height: 26px;
  color: var(--accent-text-color);
  display: block;

  @media (--not-small-vp) {
    font-size: var(--title-size);
    line-height: 36px;
  }
}

.label {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: var(--main-text-color);
  text-transform: uppercase;
  min-width: fit-content;
}

.input {
  width: 100%;
  box-sizing: border-box;
}

.input::placeholder {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: var(--main-text-color);
  text-transform: uppercase;
}

.title {
  --title-size: 36px;
  --title-big-size: 60px;
  --inner-title-size: 24px;

  font-family: var(--accent-font);
  font-weight: 400;
  color: var(--accent-text-color);
}

.description {
  --description-size: 14px;

  font-weight: 400;
  font-size: var(--description-size);
  line-height: 18px;
}

.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
}

.main-nav__item:not(:last-child) {
  margin-right: 34px;
}

.main-nav__link {
  font-family: var(--accent-font);
  text-transform: uppercase;
  text-decoration: none;
}

.offer {
  min-height: 480px;
  background:
    linear-gradient(
    rgba(104, 183, 56, 0.85) 0% 63%,
    #ffffff 63% 100%
    ),
    #4f4f54 url("../img/main_bg.jpg") no-repeat top;
  background-size: contain;
  position: relative;

  @media (--middle-vp) {
    background: var(--action-color);
    padding-left: 87px;
  }

  @media (--wide-vp) {
    position: static;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-left: 80px;
    margin-bottom: 80px;
    background: none;
  }
}

.offer::before {
  position: absolute;
  content: "";
  background:
    linear-gradient(
    rgba(104, 183, 56, 0.85),
    rgba(104, 183, 56, 0.85)),
    #4f4f54 url("../img/main_bg.jpg") no-repeat top;
  background-size: contain;

  @media (--middle-vp) {
    display: none;
  }

  @media (--wide-vp) {
    top: 0;
    right: 0;
    min-height: 694px;
    background:
      linear-gradient(
      rgba(104, 183, 56, 0.85),
      rgba(104, 183, 56, 0.85)),
      #4f4e53 url("../img/main_bg.jpg") no-repeat;
    background-size: contain;
    background-position: top 0 left 180px;
    left: 50%;
  }
}

.offer::after {
  position: absolute;
  content: "";
  top: 167px;
  left: 50%;
  transform: translateX(-50%);
  width: 270px;
  height: 280px;
  background: url("../img/catenergy_box.png") no-repeat;
  background-size: contain;

  @media (--middle-vp) {
    top: 255px;
    width: 709px;
    height: 675px;
    background-size: cover;
  }

  @media (--wide-vp) {
    position: relative;
    transform: translateX(0);
    width: 552px;
    height: 499px;
    top: 40px;
    left: -205px;
    background-size: cover;
  }
}

.offer__chapter {
  display: flex;
  flex-direction: column;
  min-height: 480px;
  margin-bottom: 20px;

  @media (--middle-vp) {
    min-height: 920px;
    order: 2;
    margin-bottom: 0;
  }

  @media (--wide-vp) {
    min-height: 553px;
  }
}

.offer__title {
  font-size: var(--title-size);
  line-height: 36px;
  color: var(--action-color);
  text-align: center;
  padding: 27px 0 0 0;
  margin: 0 0 25px 0;

  @media (--not-small-vp) {
    font-size: var(--title-big-size);
    line-height: 60px;
    color: var(--accent-text-color);
    text-align: left;
    padding-top: 62px;
    margin-bottom: 41px;
  }

  @media (--wide-vp) {
    padding-top: 114px;
    margin-bottom: 38px;
  }
}

.offer__text {
  font-family: var(--accent-font);
  line-height: 14px;
  color: var(--action-color);
  text-transform: uppercase;
  text-align: center;
  padding: 0;
  margin: 0 0 49px 0;

  @media (--not-small-vp) {
    font-size: 20px;
    line-height: 20px;
    color: var(--accent-text-color);
    text-align: left;
  }

  @media (--wide-vp) {
    margin: 0 0 54px 0;
  }
}

.offer__button {
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  min-width: 100%;
  padding: 11px 0;
  margin-top: auto;
  z-index: 1;

  @media (--not-small-vp) {
    font-size: 20px;
    min-width: 244px;
    padding: 14px 0;
    margin: 0;
  }
}

.options__list {
  list-style: none;
  padding: 0;
  margin: 0;

  @media (--wide-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 80px;
  }
}

.options__item {
  padding: 21px 33px 18px 19px;
  background-color: var(--accent-bg-color);
  margin-bottom: 20px;

  @media (--middle-vp) {
    padding: 77px 28px 53px 52px;
    margin-bottom: 30px;
  }
}

.options__title {
  font-size: var(--inner-title-size);
  line-height: 37px;
  text-transform: uppercase;
  padding: 0;
  margin: 0;
  margin-bottom: 29px;
}

.options__description {
  padding: 0;
  margin: 0;
  margin-bottom: 38px;

  @media (--middle-vp) {
    max-width: 340px;
  }
}

.options__link {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: var(--additional-text-color);
  text-transform: uppercase;
  text-decoration: none;
}

.process__chapter {
  margin-bottom: 49px;
}

.process__title {
  font-size: var(--title-size);
  line-height: 40px;
  padding: 0;
  margin: 0;
  margin-bottom: 40px;
}

.process__list {
  list-style: none;
  padding: 0;
  margin: 0;

  @media (--middle-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 37px 60px;
  }

  @media (--wide-vp) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 0 80px;
  }
}

.process__item:not(:last-child) {
  margin-bottom: 24px;
}

.process__description {
  margin: 0;
}

.example {
  background-color: #eaeaea;
}

.example__chapter {
  padding: 25px 0 40px;

  @media (--wide-vp) {
    display: flex;
    justify-content: space-between;
  }
}

@media (--wide-vp) {
  .example__feature {
    max-width: 440px;
  }
}

.example__title {
  font-size: var(--title-size);
  line-height: 40px;
  padding: 0;
  margin: 0;
  margin-bottom: 40px;
}

.example__description {
  margin: 0 0 35px 0;
}

.example__list {
  margin: 0;
}

.stats {
  display: grid;
  grid-template-columns: 1fr 1fr;

  @media (--middle-vp) {
    grid-template-columns: 162px 162px 1fr;
    gap: 0 42px;
  }
}

.stats__item {
  margin-bottom: 20px;
}

.stats__item:last-child {
  margin-bottom: 40px;
}

.stats__description {
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  margin: 0;
}

.stats__number {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: var(--accent-text-color);
  margin-bottom: 8px;
}

.location {
  background-color: var(--accent-bg-color);
  padding: 26px 0;

  &--main_background {
    background-color: var(--action-color);
  }

  @media (--wide-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 80px;
  }
}

.location__chapter {
  display: flex;
  justify-content: space-between;

  @media (--middle-vp) {
    padding: 0 40px;
  }

  @media (--wide-vp) {
    display: grid;
    gap: 0 80px;
  }
}

.location__suggestion {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #111111;
  padding: 0;
  margin: 0 10px 0 0;
  text-transform: uppercase;

  @media (--middle-vp) {
    line-height: 26px;
    max-width: 177px;
  }

  @media (--wide-vp) {
    padding: 56px 0 56px 80px;
    margin: 0;
  }
}

.address {
  line-height: 20px;
  font-style: normal;

  @media (--not-small-vp) {
    max-width: 160px;
    text-align: right;
  }

  @media (--wide-vp) {
    grid-column: 2 / 3;
    text-align: left;
    padding: 56px 80px 56px 0;
  }
}

.questionnaire__chapter {
  margin-bottom: 32px;
}

.questionnaire__title {
  font-size: var(--title-size);
  line-height: 36px;
  padding: 0;
  margin: 0;
  margin-bottom: 37px;
}

.questionnaire__description {
  font-family: var(--accent-font);
  color: var(--accent-text-color);
  text-transform: uppercase;
  margin-bottom: 39px;
}

.form__indent {
  padding: 0;
  margin: 0;
}

.form__info-wrapper {
  @media (--wide-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 80px;
    margin-bottom: 65px;
  }
}

.form__general-info {
  margin-bottom: 50px;
}

.form__list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.form__choice {
  margin-bottom: 45px;

  @media (--not-small-vp) {
    padding: 25px 28px;
    margin-bottom: 71px;
  }

  @media (--wide-vp) {
    margin-bottom: 0;
  }
}

.form__contacts {
  margin-bottom: 23px;

  @media (--middle-vp) {
    margin-bottom: 65px;
  }
}

.form__extra {
  margin-bottom: 34px;
}

.form__button {
  width: 100%;
  padding: 0;
  margin: 0;
  margin-bottom: 21px;

  @media (--not-small-vp) {
    grid-column: 1 / 3;
  }
}

.form__result {
  @media (--not-small-vp) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 0 60px;
  }
}

.form__footnote {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  margin-bottom: 32px;

  @media (--not-small-vp) {
    grid-column: 3 / 5;
    text-align: left;
  }
}

.general-list__item {
  display: flex;
}

.general-list__item:not(:last-child) {
  margin-bottom: 12px;
}

.general-list__label {
  margin-right: 13px;
}

.choice-list__item:not(:last-child) {
  margin-bottom: 12px;
}

.choice-list__label {
  color: var(--additional-text-color);
}

.contacts-list {
  @media (--wide-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 80px;
  }
}

.contacts-list__item {
  display: flex;
}

.contacts-list__item:not(:last-child) {
  margin-bottom: 14px;
}

.contacts-list__label {
  margin-right: 14px;
}

.extra-list {
  @media (--not-small-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 32px 60px;
  }

  @media (--wide-vp) {
    grid-template-columns: repeat(4, 1fr);
    gap: 0 80px;
  }
}

.extra-list__item:not(:last-child) {
  margin-bottom: 12px;
}

.extra-list__label {
  color: var(--accent-text-color);

  @media (--not-small-vp) {
    font-size: 20px;
    line-height: 30px;
  }
}

.comment__chapter {
  margin-bottom: 27px;
}

.comment__legend {
  margin-bottom: 38px;
}

.main-inner__title {
  font-size: var(--title-size);
  line-height: 36px;
  padding: 0;
  margin: 0 0 49px 0;
}

.catalog__table {
  display: grid;
  margin-bottom: 50px;
}

.catalog__button {
  display: block;
  width: 100%;
}

.table {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(245px, 1fr));
  gap: 40px;

  @media (--middle-vp) {
    gap: 60px;
  }

  @media (--wide-vp) {
    gap: 80px;
  }
}

.table__line {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 33px;

  &--show_more {
    grid-template-columns: 1fr;
  }

  @media (--not-small-vp) {
    grid-template-columns: 1fr;
    padding: 0 36px 39px;
  }
}

.table__line:last-child {
  margin-top: 50px;
}

.table__title {
  padding: 0;
  margin: 0;
  margin-bottom: 14px;

  @media (--not-small-vp) {
    grid-row: 2/3;
  }
}

.table__link {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--additional-text-color);
  display: block;
  text-decoration: none;
  text-transform: uppercase;

  &--show_position {
    text-align: center;
    margin-bottom: 8px;
  }
}

.table__cell {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  padding: 0;
  margin: 0;

  &--text_position {
    text-align: center;
    margin-bottom: 21px;
  }
}

.table__image {
  display: block;
  grid-area: 1 / 1 / 5 / 2;

  @media (--not-small-vp) {
    grid-row: 1 / 2;
    text-align: center;
  }
}

.table__image-link {
  display: block;
}

.table__button {
  grid-column: 1 / -1;
}

.add-goods__table {
  margin-bottom: 54px;
}

.add-goods__title {
  font-size: var(--inner-title-size);
  line-height: 24px;
  padding: 0;
  margin: 0;
  margin-bottom: 49px;
}

.add-goods__wrapper {
  @media (--wide-vp) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 0 80px;
  }
}

.add-goods__title {
  @media (--not-small-vp) {
    font-size: var(--title-size);
    margin-bottom: 80px;
  }
}

.add-goods__table {
  display: grid;
  grid-template-columns: 1fr;

  @media (--wide-vp) {
    grid-column: 1 / 4;
    margin-bottom: 0;
  }
}

.add-table__line {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 31px;

  @media (--middle-vp) {
    grid-template-columns: repeat(4, 1fr);
    gap: 0 60px;
    padding: 0 37px;
  }

  @media (--wide-vp) {
    grid-template-columns: 325px 245px 80px 245px;
    width: 895px;
  }
}

.add-table__title {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: var(--additional-text-color);
  text-transform: uppercase;
  margin-bottom: 13px;
  grid-column: 1 / 3;
  text-align: left;

  @media (--wide-vp) {
    grid-column: 1 / 2;
  }
}

.add-table__cell {
  font-weight: 400;
  font-size: var(--description-size);
  line-height: 14px;
  margin-bottom: 15px;

  &--priсe {
    text-align: right;

    @media (--wide-vp) {
      text-align: left;
    }
  }
}

.add-table__cell--volume {
  @media (--middle-vp) {
    text-align: right;
    margin-right: -80px;
  }

  @media (--wide-vp) {
    text-align: left;
  }
}

.add-table__button {
  grid-column: 1 / -1;
  margin: 0;

  @media (--wide-vp) {
    grid-column: 4 / 5;
  }
}

.add-goods__button {
  display: block;
  width: 100%;
  padding: 10px 0;
}

.present {
  min-height: 290px;
  position: relative;

  @media (--wide-vp) {
    grid-column: 4 / 5;
  }
}

.present::before {
  position: absolute;
  content: "";
  width: 100%;
  min-height: 290px;
  background:
    linear-gradient(
    rgba(104, 183, 56, 0.85),
    rgba(104, 183, 56, 0.85)),
    url("../img/cat_case.jpg") no-repeat center;
  background-size: cover;
  z-index: -1;
}

.present__chapter {
  margin-bottom: 35px;
}

.present__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--action-color);
  text-align: center;
  max-width: 161px;
  padding: 0;
  margin: 0;
}
